.home {
  width: calc(100vw - 11.5vw);
  height: calc(100vh - 120px);
  padding: 1.5vw;
  padding-left: 10vw;

  display: flex;
  justify-content: flex-start;
  align-items: center;

  background-image: url(./../../assets/img/bgc.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.home-description {
  color: rgb(255, 255, 255);

  &__first-name {
    font-size: 4em;
  }

  &__second-name {
    font-size: 5em;
    font-weight: 700;
  }

  &__job-title {
    font-size: 2em;
    font-weight: 400;
  }

  &__btns {
    width: 100%;
    padding: 40px 0;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
  }

  &__btn {
    width: 40%;
    padding: 2px 0;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 10px;

    font-size: 1.5em;
    font-weight: 400;
    text-align: center;
  }
}
